    },
    createSomething() {
      this.$emit('create')
    },
    toPersonalCenter() {
      this.$router.push('/personalCenter')
    },
    logout() {
      api
        .logout()
        .then((data) => {
          console.log(data)
          removeCookie('uid')
          removeCookie('userInfo')
          this.$router.push('/login')
        })
        .catch((error) => {
          console.log(error)
        })
    }
  },
  components: {

